.container{
  width: 100%;
  min-height: 91vh;
  margin-top: 10px;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.122);
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-column-gap: 50px;
  position: relative;
  padding: 40px 100px;
}
.container>img{
  position: absolute;
  right: 10px;
  top: 10px;
  cursor: pointer;
}
.container>img:hover{
  opacity: 0.5;
}
@media screen and (max-width:1000px) {
  .container{
    grid-template-columns: 1fr;
  }
}
@media screen and (max-width:550px) {
  .container{
      padding: 25px 25px;
  }
  .container>img{
    position: absolute;
    right: 25px;
    height: 25px;
    background-color: black;
    border-radius: 50%;

  }
}